<template>
    <div class="joining_container">
        <LoaderComponent v-if="loading" />
        <div v-else class="main_container">
            <input
                @keypress="sendCode"
                v-model="gameCode"
                type="text"
                autofocus
            />
        </div>
    </div>
</template>
<script>
import socketIO from "@/socket";

import LoaderComponent from "@/components/LoaderComponent.vue";

export default {
    components: {
        LoaderComponent,
    },
    created() {
        this.gameCode =
            new URLSearchParams(window.location.search).get("gamecode") || "";
        this.socket = socketIO;
    },
    data() {
        return {
            gameCode: "",
            loading: true,
            socket: null,
        };
    },
    methods: {
        async joinGame() {
            this.socket.emit("join_game", this.gameCode, ({ status }) => {
                if (status === 200) {
                    this.$router.replace({ name: "selection" });
                } else {
                    this.loading = false;
                    this.errorToast();
                }
            });
        },
        sendCode(e) {
            e.stopPropagation();
            e.preventDefault();
            if (e.charCode === 13) {
                this.joinGame();
            }
        },
        errorToast() {
            const oldToast = document
                .getElementById("app")
                .querySelector(".toast");
            if (oldToast) oldToast.remove();

            const toast = document.createElement("div");

            toast.classList.add("toast");

            toast.innerHTML = `
				<span class="material-symbols-rounded toast_icon">error</span>
				<h3 class="toast_text">Wrong Code</h3>
				<div class="toast_timer"></div>
			`;

            document.getElementById("app").appendChild(toast);
            toast.style.left = `${
                document.getElementById("app").offsetWidth / 2 -
                toast.offsetWidth / 2
            }px`;
            setTimeout(() => {
                toast.remove();
            }, 1500);
        },
    },
    mounted() {
        if (this.gameCode === "") this.loading = false;
        else this.joinGame();
    },
    name: "JoiningComponent",
};
</script>
<style lang="scss" scoped>
.joining_container {
    height: 100%;
    width: 100%;

    .main_container {
        align-items: center;
        display: flex;
        flex-direction: column;
        height: 100%;
        justify-content: center;
        width: 100%;

        input {
            background-color: #151515;
            border-radius: 5px;
            border: none;
            color: #ccc;
            font-size: 1.3em;
            padding: 10px 0;
            text-align: center;

            &:focus {
                color: #fff;
                outline: none;
                box-shadow: 0 0 5px #444 inset, 0 0 5px #888;
            }
        }
    }
}
</style>
